const board1 = document.querySelector('.to_do')
const board2 = document.querySelector('.in_progress')
const board3 = document.querySelector('.done')

const cardDrop = document.querySelector('#card_drop')

board1.ondragover = allowDrop
board2.ondragover = allowDrop
board3.ondragover = allowDrop

function allowDrop (event) {
  event.preventDefault()
}

cardDrop.ondragStart = drag

function drag (event) {
  event.dataTransfer.setData('id', event.target.id)
}

board1.ondrop = drop
board2.ondrop = drop
board3.ondrop = drop

function drop (event) {
  const itemId = event.dataTransfer.getData('id')
  console.log(itemId)
  event.target.append(document.getElementById(itemId))
}
